* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.state {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 100px auto;
  border: 2px solid #333;
  padding: 30px;
  text-align: center;
  background-color: tomato;
  color: #fff;
}

.state h1 {
  font-size: 50px;
  margin-bottom: 30px;
}

.state input {
  width: 300px;
  height: 30px;
  margin: 0 auto;
  display: block;
  padding: 0 10px;
  border: 0;
}

.state button {
  width: 100px;
  height: 30px;
  border: 0;
  margin-top: 10px;
  transition: 0.5s;
  cursor: pointer;
}

.state button:hover {
  background-color: #333;
  color: #fff;
  transition: 0.5s;
  border-radius: 30px;
}

.state p {
  border: 1px solid #fff;
  margin-top: 30px;
  height: 300px;
  background-color: #fff;
  color: #333;
  text-align: left;
}

